export default class ExternalLinks {
  constructor(links) {
    this.links = links

    this.setUp()
  }

  setUp() {
    for (let i = 0, len = this.links.length; i < len; i++) {
      this.testLink(this.links[i])
    }
  }

  testLink(link) {
    if (link.hostname !== '' && link.hostname !== window.location.hostname) {
      this.makeExternal(link)
    }
  }

  makeExternal(link) {
    let content = link.innerHTML

    link.setAttribute('rel', 'noopener noreferrer')
    link.setAttribute('target', '_blank')
    link.classList.add('external-link')

    link.innerHTML = `${content}<span class="a11y-sr-only"> (opens in new window)</span>`
  }
}



